/*******************************
         Site Settings
*******************************/

:root {

/*-------------------
       Fonts
--------------------*/

--font-name: 'Lato';
--font-smoothing: antialiased;

--header-font: var(--font-name), 'Helvetica Neue', Arial, Helvetica, sans-serif;
--page-font: var(--font-name), 'Helvetica Neue', Arial, Helvetica, sans-serif;

--bold: bold;
--normal: normal;

/*-------------------
      Global Size
--------------------*/

/*
  All UI is RELATIVELY SIZED in EMs
  Sizing up this base unit will increase the size of "medium"
  for all UI

  It is common for dense web apps to use 13px and more friendly apps to use 14px
  but for blog/marketing it may make sense to increase up to 16px
*/

--em-size: 14;
--font-size: 14px;

/*-------------------
        Sizes
--------------------*/

/*
  These are all the other sizes as unitless values expressed relative to the base em

  To make EM sizes WHOLE pixel values, modify the NUMERATOR to the desired pixel value
  and the DENOMINATOR to the base em size

  We dont use a var for the denominator otherwise toggling EM size var would not scale
  since the ratios would be skewed

  @small = 13/14 = 0.928rem = 13px
*/

--mini-ratio: 11 / 14;
--tiny-ratio: 12 / 14;
--small-ratio: 13 / 14;
--medium-ratio: 14 / 14;
--large-ratio: 16 / 14;
--big-ratio: 18 / 14;
--huge-ratio: 20 / 14;
--massive-ratio: 24 / 14;

/*-------------------
    Border Radius
--------------------*/

/* See Power-user section below
   for explanation of px variables
*/
--relative-border-radius: var(--relative4px);
--absolute-border-radius: var(--4px);

--default-border-radius: var(--absolute-border-radius);

/*-------------------
    Brand Colors
--------------------*/

--primary-color: var(--blue);
--secondary-color: var(--black);

--light-primary-color: var(--light-blue);
--light-secondary-color: var(--light-black);

/*--------------
  Page Heading
---------------*/

--header-font-weight: var(--bold);
--header-line-height: calc((18 / 14) * 1em);

--h1: calc(28 / 14 * 1rem);
--h2: calc(24 / 14 * 1rem);
--h3: calc(18 / 14 * 1rem);
--h4: calc(15 / 14 * 1rem);
--h5: calc(14 / 14 * 1rem);

/*--------------
   Form Input
---------------*/

/* This adjusts the default form input across all elements */
--input-background: var(--white);
--input-vertical-padding: var(--relative11px);
--input-horizontal-padding: var(--relative14px);
--input-padding: var(--input-vertical-padding) var(--input-horizontal-padding);

/* Input Text Color */
--input-color: var(--text-color);
--input-placeholder-color: lighten(var(--input-color), 75);
--input-placeholder-focus-color: lighten(var(--input-color), 45);

/* Line Height Default For Inputs in Browser (Descenders are 17px at 14px base em) */
--input-line-height: calc((17 / 14) * 1em);

/*-------------------
    Focused Input
--------------------*/

/* Used on inputs, textarea etc */
--focused-form-border-color: #85B7D9;

/* Used on dropdowns, other larger blocks */
--focused-form-muted-border-color: #96C8DA;



/*-------------------
        Page
--------------------*/

--page-background: #FFFFFF;
--page-overflow-x: hidden;

--line-height: 1.4285em;
--text-color: rgba(0, 0, 0, 0.87);

/*-------------------
      Paragraph
--------------------*/

--paragraph-margin: 0em 0em 1em;
--paragraph-line-height: var(--line-height);

/*-------------------
       Links
--------------------*/

--link-color: #4183C4;
--link-underline: none;
--link-hover-color: darken(saturate(var(--link-color), 20), 15, relative);
--link-hover-underline: var(--link-underline);

/*-------------------
    Scroll Bars
--------------------*/

--use-custom-scrollbars: true;

--custom-scrollbar-width: 10px;
--custom-scrollbar-height: 10px;

--track-background: rgba(0, 0, 0, 0.1);
--track-border-radius: 0px;

--thumb-border-radius: 5px;
--thumb-background: rgba(0, 0, 0, 0.25);
--thumb-transition: color 0.2s ease;

--thumb-inactive-background: rgba(0, 0, 0, 0.15);
--thumb-hover-background: rgba(128, 135, 139, 0.8);

/* Inverted */
--track-inverted-background: rgba(255, 255, 255, 0.1);
--thumb-inverted-background: rgba(255, 255, 255, 0.25);
--thumb-inverted-inactive-background: rgba(255, 255, 255, 0.15);
--thumb-inverted-hover-background: rgba(255, 255, 255, 0.35);

/*-------------------
  Highlighted Text
--------------------*/

--highlight-background: #CCE2FF;
--highlight-color: var(--text-color);

--input-highlight-background: rgba(100, 100, 100, 0.4);
--input-highlight-color: var(--text-color);


/*-------------------
       Loader
--------------------*/

--loader-size: var(--relative-big);
--loader-speed: 0.6s;
--loader-line-width: 0.2em;
--loader-fill-color: rgba(0, 0, 0, 0.1);
--loader-line-color: var(--grey);

--inverted-loader-fill-color: rgba(255, 255, 255, 0.15);
--inverted-loader-line-color: var(--white);

/*-------------------
        Grid
--------------------*/

--column-count: 16;

/*-------------------
     Transitions
--------------------*/

--default-duration: 0.1s;
--default-easing: ease;

/*-------------------
     Breakpoints
--------------------*/

--mobile-breakpoint: 320px;
--tablet-breakpoint: 768px;
--computer-breakpoint: 992px;
--large-monitor-breakpoint: 1200px;
--widescreen-monitor-breakpoint: 1920px;

/*-------------------
      Site Colors
--------------------*/

/*---  Colors  ---*/
--red: #DB2828;
--orange: #F2711C;
--yellow: #FBBD08;
--olive: #B5CC18;
--green: #21BA45;
--teal: #00B5AD;
--blue: #2185D0;
--violet: #6435C9;
--purple: #A333C8;
--pink: #E03997;
--brown: #A5673F;
--grey: #767676;
--black: #1B1C1D;

/*---  Light Colors  ---*/
--light-red: #FF695E;
--light-orange: #FF851B;
--light-yellow: #FFE21F;
--light-olive: #D9E778;
--light-green: #2ECC40;
--light-teal: #6DFFFF;
--light-blue: #54C8FF;
--light-violet: #A291FB;
--light-purple: #DC73FF;
--light-pink: #FF8EDF;
--light-brown: #D67C1C;
--light-grey: #DCDDDE;
--light-black: #545454;

/*---   Neutrals  ---*/
--full-black: #000000;
--off-white: #F9FAFB;
--dark-white: #F3F4F5;
--mid-white: #DCDDDE;
--white: #FFFFFF;

/*--- Colored Backgs ---*/
--red-background: #FFE8E6;
--orange-background: #FFEDDE;
--yellow-background: #FFF8DB;
--olive-background: #FBFDEF;
--green-background: #E5F9E7;
--teal-background: #E1F7F7;
--blue-background: #DFF0FF;
--violet-background: #EAE7FF;
--purple-background: #F6E7FF;
--pink-background: #FFE3FB;
--brown-background: #F1E2D3;

/*--- Colored Headers ---*/
--red-header-color: darken(var(--red-text-color), 5);
--olive-header-color: darken(var(--olive-text-color), 5);
--green-header-color: darken(var(--green-text-color), 5);
--yellow-header-color: darken(var(--yellow-text-color), 5);
--blue-header-color: darken(var(--blue-text-color), 5);
--teal-header-color: darken(var(--teal-text-color), 5);
--pink-header-color: darken(var(--pink-text-color), 5);
--violet-header-color: darken(var(--violet-text-color), 5);
--purple-header-color: darken(var(--purple-text-color), 5);
--orange-header-color: darken(var(--orange-text-color), 5);
--brown-header-color: darken(var(--brown-text-color), 5);

/*--- Colored Text ---*/
--red-text-color: var(--red);
--orange-text-color: var(--orange);
--yellow-text-color: #B58105; /* Yellow text is difficult to read */
--olive-text-color: #8ABC1E; /* Olive is difficult to read */
--green-text-color: #1EBC30; /* Green is difficult to read */
--teal-text-color: #10A3A3; /* Teal text is difficult to read */
--blue-text-color: var(--blue);
--violet-text-color: var(--violet);
--purple-text-color: var(--purple);
--pink-text-color: var(--pink);
--brown-text-color: var(--brown);

/*--- Colored Border ---*/
--red-border-color: var(--red-text-color);
--orange-border-color: var(--orange-text-color);
--yellow-border-color: var(--yellow-text-color);
--olive-border-color: var(--olive-text-color);
--green-border-color: var(--green-text-color);
--teal-border-color: var(--teal-text-color);
--blue-border-color: var(--blue-text-color);
--violet-border-color: var(--violet-text-color);
--purple-border-color: var(--purple-text-color);
--pink-border-color: var(--pink-text-color);
--brown-border-color: var(--brown-text-color);

/*-------------------
     Alpha Colors
--------------------*/

--subtle-transparent-black: rgba(0, 0, 0, 0.03);
--transparent-black: rgba(0, 0, 0, 0.05);
--strong-transparent-black: rgba(0, 0, 0, 0.10);
--very-strong-transparent-black: rgba(0, 0, 0, 0.15);

--subtle-transparent-white: rgba(255, 255, 255, 0.02);
--transparent-white: rgba(255, 255, 255, 0.08);
--strong-transparent-white: rgba(255, 255, 255, 0.15);

/*-------------------
       Accents
--------------------*/

/* Differentiating Neutrals */
--subtle-gradient: linear-gradient(transparent, var(--transparent-black));

/* Differentiating Layers */
--subtle-shadow:
  0px 1px 2px 0 var(--border-color)
;
--floating-shadow:
  0px 2px 4px 0px rgba(34, 36, 38, 0.12),
  0px 2px 10px 0px rgba(34, 36, 38, 0.15)
;

