/*-------------------
      Borders
--------------------*/

--circular-radius: 500rem;

--border-color: rgba(34, 36, 38, 0.15);
--strong-border-color: rgba(34, 36, 38, 0.22);
--internal-border-color: rgba(34, 36, 38, 0.1);
--selected-border-color: rgba(34, 36, 38, 0.35);
--strong-selected-border-color: rgba(34, 36, 38, 0.5);
--disabled-border-color: rgba(34, 36, 38, 0.5);

--solid-internal-border-color: #FAFAFA;
--solid-border-color: #D4D4D5;
--solid-selected-border-color: #BCBDBD;

